  width: 75px;
  height: 75px;
  border: 12px solid #fff;
  border-color: #01b4e4 #fff #1ed5a9;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  position: absolute;
  top: 50%;
  left: 50%;
