  left: 0;
}

.theme-switch__marker {
  width: 20px;
  height: 20px;
  z-index: 2;
  pointer-events: none;
  background-color: #fff;
  border-radius: 50%;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: relative;
  transform: translateX(3px);
  box-shadow: 0 1px 1px #1ed5a9;
}

.theme-switch__toggle:checked + .theme-switch__track {
  background-color: radial-gradient(at 30% top, #031d33 0%, #032541 70%);
}

.theme-switch__toggle:checked ~ .theme-switch__marker {
  transform: translateX(35px);
}

